.site-container {
  @include footerToBottom;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  width: 100%;
  max-width: var(--container-width);

  &--wrapper {
    max-width: var(--wrapper-width);
  }
}

.section {
  margin-bottom: clamp(2rem, calc(2rem + 8.45vw), 11.25rem);
}

.logo {
  @include flex-all-center;

  flex-shrink: 0;
  margin-right: clamp(0.5rem, calc(0.5rem + 2.7vw), 3.75rem);

  @include for-width(-mobile-sl) {
    &.hidden {
      display: none;
    }
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.input-reset {
  border-color: transparent;
  padding: 15px 30px;
  width: 100%;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.6;
  background-color: transparent;

  &::placeholder {
    color: var(--color-gray);
  }
}

.checkboxes {
  @include flex;

  gap: clamp(0.2rem, calc(0.2rem + 1vw), 0.75rem);
  margin-bottom: rem(52);
  border: none;
  padding: 0;

  &__label {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    position: relative;
    cursor: pointer;
    font-weight: 700;
    color: var(--color-gray-700);
    background-color: var(--color-white);
    padding: rem(6) rem(16);
    border-width: 1px;
    border-style: solid;
    border-color: var(--color-gray-400);
    border-radius: var(--br);
    transition-property: color, background-color, border-color;
    transition-duration: 0.3s;
    transition-timing-function: linear;

    &:is(:hover, :focus) {
      border-color: var(--color-green);
    }
  }

  input[type="radio"]:checked+.checkboxes__label {
    border-color: var(--color-green);
    color: var(--color-white);
    background-color: var(--color-green);
  }
}

.login {
  &__btn {
    @include flex-all-center;

    position: relative;
    transition: 0.3s color linear;

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      transform: translateY(-60%);
      left: -23px;
      mask-image: url("../img/sprite.svg#profile");
      mask-position: center;
      mask-repeat: no-repeat;
      mask-size: cover;
      width: 16px;
      height: 16px;
      background-color: currentColor;
      transition: background-color 0.6s linear;
    }

    @include for-width(-mobile-l) {
      font-size: 0;
    }

    &:hover {
      color: var(--color-white);
    }
  }
}

