.countdown {
  padding-top: clamp(2rem, calc(1rem + 3.3vw), 5rem);

  &__list {
    @include flex;
    gap: 24px;
  }

  &__item {
    @include flex-all-center;

    flex-direction: column;
    font-weight: 700;
    font-size: 28px;
  }

  &__label {
    font-weight: 400;
    font-size: 14px;
  }
}

.return__list {
  list-style-type: disc;

  li {
    padding-left: 1rem;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 4px;
      height: 4px;
      background-color: var(--color-green);
      border-radius: 50%;
    }
  }
}

.share {
  display: inline-flex;
  align-items: center;
  margin-bottom: clamp(1rem, calc(1rem + 3vw), 3.2rem);
  gap: 1rem;
}

.payment__list {
  display: flex;
  gap: rem(24);
}

@keyframes slide-up {
  0% {
    transform: translateY(-5%);
  }

  100% {
    transform: translateY(0%);
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.carousel {
  position: relative;
  width: (390px);
  height: 440px;

  &__item {
    height: 440px;
    position: absolute;
    inset: 0;
    opacity: 0;
    transition: 200ms opacity ease-in-out;
    transition-delay: 200ms;

    &[data-active] {
      opacity: 1;
      z-index: 1;
      transition-delay: 0ms;
    }
  }

  &-btn {
    display: inline-flex;
    align-items: center;
    position: absolute;
    isolation: isolate;
    left: 1rem;
    z-index: 2;
    top: 50%;

    &::before {
      content: "";
      position: absolute;
      mask-position: center;
      mask-repeat: no-repeat;
      mask-size: cover;
      background-color: var(--color-gray-800);
      transition: background-color 0.3s linear;
      mask-image: url("../img/sprite.svg#chevron");
      width: 18px;
      height: 18px;
    }

    &--reverse {
      left: auto;
      right: 1rem;
      rotate: 180deg;
    }

    &:hover::before {
      background-color: var(--color-white);
    }
  }

  .article__img {
    min-height: 440px;
  }
}

.list {
  li {
    position: relative;
    padding-left: 1rem;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      border-radius: 100vw;
      width: 4px;
      height: 4px;
      background-color: var(--color-green);
      transform: translateY(-50%);
    }
  }
}

.rating {

  &__item {
    @include flex-all-center;

    position: relative;
    width: (356px);
    font-size: rem(14);
    color: var(--color-gray-700);

    &::before {
      content: "";
      position: absolute;
      left: 10px;
      mask-image: url("../img/sprite.svg#star");
      mask-position: center;
      mask-repeat: no-repeat;
      mask-size: cover;
      width: 12px;
      height: 12px;
      background-color: currentcolor;
    }

    &:not(:last-child) {
      margin-bottom: 0.65rem;
    }

    span[data-progress] {
      margin: 0 16px 0 30px;
      height: 4px;
      width: 100%;
      max-width: 356px;
      border-radius: em(45);
      background-color: var(--color-gray-300);
      overflow: hidden;
    }

    span[data-progress]::after {
      --bg-color: #03cea4;
      content: "";
      @include flex;
      background-color: var(--bg-color);
      width: 0;
      height: 100%;
      font-size: 0;
      border-radius: 40px 0 0 40px;
      animation: progressAnimation 3s linear forwards;
    }

    &:nth-child(2)span[data-progress]::after {
      --bg-color: #1fdf58;
    }

    &:nth-child(3)span[data-progress]::after {
      --bg-color: #f2ea2b;
    }

    &:nth-child(4)span[data-progress]::after {
      --bg-color: #f89828;
    }

    &:nth-child(5)span[data-progress]::after {
      --bg-color: #ff4242;
    }

    span[data-progress].animate-progress::after {
      content: attr(data-progress) "%";
      width: var(--animate-progress);
    }

    @keyframes progressAnimation {
      from {
        width: 0;
      }

      to {
        width: var(--animate-progress, 0%);
      }
    }
  }
}

.vote {
  &__list {
    @include flex;
  }

  &__item {
    @include flex-all-sb;

    &:not(:last-child) {
      margin-right: 2.2rem;
    }
  }

  &__btn {
    position: relative;
    isolation: isolate;

    &::before {
      content: "";
      left: -20px;
      transform: translateY(-50%);
      position: absolute;
      mask-position: center;
      mask-repeat: no-repeat;
      mask-size: cover;
      mask-image: url("../img/sprite.svg#like");
      width: 16px;
      height: 16px;
      background-color: currentcolor;
      transition: background-color 0.3s linear;
    }

    &:is(:hover, :active) {
      &::before {
        background-color: var(--color-green-100);
      }
    }

    &--dislike {
      &::before {
        mask-image: url("../img/sprite.svg#dislike");
      }

      &:is(:hover, :active) {
        &::before {
          background-color: var(--color-red);
        }
      }
    }
  }
}

.pagination {
  @include flex-all-center;

  &__btn {
    @include flex-all-center;

    position: relative;

    &::before {
      content: "";
      left: 0;
      // transform: translateY(-50%);
      position: absolute;
      mask-position: center;
      mask-repeat: no-repeat;
      mask-size: cover;
      mask-image: url("../img/sprite.svg#arrow");
      width: 16px;
      height: 16px;
      background-color: currentcolor;
      transition: background-color 0.3s linear;
    }

    &--reverse {
      &::before {
        rotate: 180deg;
        right: 0;
      }
    }
  }

  &__list {
    @include flex-all-center;
    padding: 0 2rem;

    li {
      &:not(:last-child) {
        margin-right: 20px;
      }
    }

    a {
      position: relative;
      font-weight: 700;
      transition: color 0.3s linear;

      &:is(:hover, [aria-current="page"]) {
        color: var(--color-green);
      }
    }
  }
}

.link {
  text-decoration-color: currentcolor;
  text-decoration-thickness: from-font;
  text-underline-offset: 3px;
  text-decoration-skip-ink: solid;
  text-decoration-line: underline;
  transition:
    text-decoration-color 0.5s linear,
    color 0.5s linear;

  &:is(:hover, :active) {
    text-decoration-color: transparent;
    color: var(--color-green-100);
  }
}

.scroll {
  overflow: inherit;
  padding-right: 10px;
  padding-bottom: 10px;
  scrollbar-color: var(--color-green) var(--color-white);
  scrollbar-width: thin;

  &::-webkit-scrollbar {
    width: 4px;
    height: 4px;
  }

  &::-webkit-scrollbar-track {
    -webkit-border-radius: 10px;
    border-radius: 10px;
    background-color: var(--color-white);
  }

  &::-webkit-scrollbar-thumb {
    -webkit-border-radius: 10px;
    border-radius: 10px;
    background-color: var(--color-green);
  }

  @include for-width(-mobile-sl) {
    overflow: auto hidden;
  }
}

